
import React, { useEffect, useCallback, useRef } from 'react'

export default function proxyHOC(WrappedComponent) {
    return function(props) {
        const ttt = useRef(null);
        useEffect(() => {
            console.log('HOC log', ttt);
            return () => {
            };
        }, []);
        const onClickHandler = useCallback(() => {
            console.log('onclick, in HOC log: ', this);
        });
        return (
            <>
                <header>1</header>
                <WrappedComponent {...props} ref={ttt} onClick={() => onClickHandler()}></WrappedComponent>
            </>
        )
    }
}
